* {
  box-sizing: border-box;
}

body {
  color: #fff;
  background-color: #000;
  margin: 0;
  padding: 0;
  font-family: Inter, sans-serif;
}

h1 {
  text-align: center;
  font-size: 1.9rem;
}

h2 {
  font-size: 1.6rem;
}

h3 {
  margin: 0;
  font-size: 1.2rem;
}

#fondo {
  background-image: url("fondo-980.287f748a.webp");
  background-position: 100% 100%;
  background-repeat: no-repeat;
  background-size: contain;
  background-attachment: fixed;
}

header {
  width: 85vw;
  height: 170px;
  color: #fff;
  vertical-align: middle;
  z-index: 1000;
  background-color: #000;
  margin: auto;
  position: sticky;
  top: 0;
  box-shadow: 0 10px 10px #000;
}

#logo {
  max-width: 850px;
  margin: auto;
  padding-top: 15px;
}

#logo img {
  vertical-align: middle;
  width: 120px;
  height: 91px;
  margin-right: 40px;
}

#logo span {
  vertical-align: middle;
  font-family: Metal Mania, cursive;
  font-size: 3.4rem;
}

header input {
  display: none;
}

header label {
  color: #fff;
  font-size: 40px;
  display: none;
  position: absolute;
  top: 20px;
  right: 0;
}

nav {
  padding: 15px 0;
}

nav ul {
  max-width: 850px;
  margin: auto;
  padding: 10px 0 0;
  list-style: none;
  display: flex;
}

nav li {
  width: 100%;
  height: 2.4rem;
  background-color: #000;
  border: 1px solid #fff;
  justify-content: center;
  align-items: center;
  display: flex;
}

nav li a {
  color: #fff;
  text-align: center;
  width: 100%;
  height: 2.4rem;
  font-weight: bold;
  line-height: 2.4rem;
  text-decoration: none;
  display: block;
}

.active {
  background-color: #9c1010;
}

@media screen and (max-width: 767px) {
  h1 {
    text-align: center;
    font-size: 1.5rem;
  }

  h2 {
    font-size: 1.3rem;
  }

  h3 {
    font-size: 1.1rem;
  }

  #logo {
    padding-top: 10px;
  }

  #logo img {
    width: 100px;
    height: 76px;
    margin-right: 30px;
  }

  #logo span {
    font-size: 2.6rem;
  }

  header {
    width: 86vw;
    height: 100px;
  }

  nav {
    padding: 0;
  }

  nav ul {
    display: none;
  }

  header label, input:checked ~ ul {
    display: block;
  }
}

@media screen and (max-width: 576px) {
  #logo img {
    width: 70px;
    height: 53px;
    margin-right: 12px;
  }

  #logo span {
    font-size: 1.7rem;
  }

  header {
    width: 86vw;
    height: 75px;
  }

  header label {
    font-size: 35px;
    top: 12px;
  }
}

main, article, footer {
  width: 85vw;
  max-width: 850px;
  margin: 0 auto;
}

.margen {
  margin: 1.6rem 0 3rem;
}

.contenedor {
  width: 100%;
  text-align: justify;
  background-color: rgba(0, 0, 0, .8);
  border: 1px solid #fff;
  margin-bottom: 2rem;
  padding: 1.1rem 1.8rem 1.8rem 1.5rem;
}

li {
  margin-bottom: .3rem;
}

#portadaDiscografia {
  width: 100%;
  max-width: 490px;
  margin: 0 auto;
  transition: all .1s linear;
  display: block;
}

#portadaDiscografia img {
  width: 100%;
  animation-name: aparecimiento;
  animation-duration: 1.5s;
}

.flexDiscos, .flexVideos {
  width: 90%;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  margin: auto;
  display: flex;
}

.flexDiscos img, .flexVideos iframe {
  width: 90%;
  margin: 1rem;
}

.flexDiscos img {
  opacity: .7;
  transition: all .3s linear;
}

.flexDiscosItem {
  flex: 30%;
}

.flexVideosItem {
  flex: 2 40%;
  display: block;
}

.flexVideosItem iframe {
  width: 90%;
  height: 250px;
  border: none;
}

.enlaceBasico, .enlaceSubrayado {
  color: #fff;
  transition: all .3s linear;
}

.enlaceBasico {
  text-decoration: none;
}

@media screen and (max-width: 767px) {
  .contenedor {
    width: 100%;
    padding: 1rem 1.1rem 1.3rem;
    font-size: .9rem;
  }

  .margen {
    margin: .7rem 0 4rem;
  }

  #portadaDiscografia {
    max-width: 368px;
  }

  .flexDiscosItem {
    flex: 1 2 50%;
  }

  .flexVideos {
    display: block;
  }

  .flexVideosItem iframe {
    width: 90%;
    margin: 10px 5%;
  }
}

@media screen and (max-width: 576px) {
  .flexDiscos {
    display: block;
  }

  .flexDiscos img {
    width: 90%;
    margin: 10px 5%;
  }

  .flexVideosItem iframe {
    height: 160px;
  }
}

#footer {
  flex-wrap: wrap;
  display: flex;
}

#footer > div {
  text-align: center;
  flex: 30%;
  padding: 15px 0 0;
}

#footerLogo img {
  width: 160px;
  height: 121px;
}

#footer ul {
  padding: 0;
  list-style: none;
}

#footer a {
  color: #fff;
  text-decoration: none;
  transition: all .3s linear;
}

@media screen and (max-width: 576px) {
  #footer {
    display: block;
  }

  #footerLogo img {
    width: 130px;
    height: 98px;
  }
}

